/* blog */
#blog{
  text-align: left;
}
.blogBox{
  width:46rem;
  height:70rem;
  padding-top:4rem;
  margin:0 auto;
  position:relative;
  padding-bottom:4rem;
}
.blogBox>li{
  width:22rem;
  height:22rem;
  text-align: left;
  box-shadow: 5px 11px 30px rgba(0,0,0,0.3);
  border-radius: 5%;
  background:#fff;
  position:absolute;
}
.date{
    border-radius: 5%;
}
.blogBox>li:nth-child(1){
    top:4rem;
    left:0;
}
.blogBox>li:nth-child(2){
    top:4rem;
    right:0;
}
.blogBox>li:nth-child(3){
    top:28rem;
    left:0;
}
.blogBox>li:nth-child(4){
    top:28rem;
    right:0;
}
.blogBox>li:nth-child(5){
    top:52rem;
    left:0;
}
.date>dt{
    padding:4rem 1rem;
    font-size:0.95rem;
    font-weight: bold;
}
.date>dd{
    height:11rem;
    padding-bottom:1rem;
    border-radius:0 0 5% 5%;
    cursor: pointer;
}
.date01>dd{
    background-color: #ff7070;
    background-image: url('./img/blog/back01.png');
    background-size: cover;
}
.date02>dd{
    background-color: #ffa51f;
    background-image: url('./img/blog/back02.png');
    background-size: cover;
}
.date03>dd{
    background-color: #ffe02f;
    background-image: url('./img/blog/back03.png');
background-size: cover;
}
.date04>dd{
    background-color: #25b00a;
    background-image: url('./img/blog/back04.png');
background-size: cover;
}

.date05>dd{
    background-color: #70bcff;
    background-image: url('./img/blog/back05.png');
    background-size: cover;
}