/* portfolio */
#portfolio{
    position:relative;
    width:100%;
    padding-top:3rem;
    box-sizing: border-box;
    display:table;
    text-align: center;
  }
  .box{
    width:67.5rem;
    display: flex;
    box-sizing: border-box;
    margin:0 auto;
  }
  .box>li{
    width:18.75rem;
  }
  .box>li:not(:last-child){
    margin-right:4rem;
  }
  .card{
    position:relative;
    width:22rem;
    height:21rem;
    box-sizing: border-box;
    box-shadow: 5px 11px 30px rgba(0,0,0,0.3);
    background-color: #fff;
    text-align: left;
    font-size:1rem;
    cursor: pointer;
    margin-top:1rem;
    border-radius:5%;
    transition:0.1s ease-in-out;
  }
  p.img{
    border-radius:0 0 5% 5%;
    padding:1rem 1rem 4.7rem;
    background-color: #f7fa30;
    margin-top:1rem;
  }
  .card02>p.img{
    display:table;
    padding:1rem;
  }
  .card02>p.img>img{
    width:20rem;
    margin:0 auto;
  }
  .card:hover{
    margin-top:0.5rem;
  }
  .card>dl>dt{
  padding:3rem 2rem 0;
  } 
  .card>dl>dd{
    padding:0 2rem 2rem;
    margin-top:1rem;
  }
  .spanText{
    font-weight:bold;
  }
  .detail{
    font-family: 'Gowun Batang', serif;
    font-size:0.88rem;
  }
  .adress{
    padding-top:1.2rem;
  }
  /* tablet_size css */
  .Tbox{
    display: flex;
  }
  .cardT{
    position:absolute;
    width:18rem;
    height:21rem;
    box-sizing: border-box;
    box-shadow: 5px 11px 30px rgba(0,0,0,0.3);
    background-color: #fff;
    text-align: left;
    font-size:0.88rem;
    cursor: pointer;
    margin-top:1rem;
    border-radius:5%;
    transition:0.1s ease-in-out;
    top:4rem;
  }
  p.imgT{
    border-radius:0 0 5% 5%;
    padding:1rem 1rem 4rem;
    background-color: #f7fa30;
    margin-top:1rem;
  }
  .cardT02>p.imgT{
    display:table;
    padding:1rem;
    margin-top:1rem;
  }
  .cardT02>p.img>img{
    width:16rem;
    margin:0 auto;
  }
  .cardT:hover{
    margin-top:0.5rem;
  }
  .cardT>dl>dt{
  text-align: center;
  padding:2rem 1rem 0;
  } 
  .cardT>dl>dd{
    padding:0 1rem 2rem;
    margin-top:1rem;
  }
  .spanText{
    font-weight:bold;
  }
  .detail{
    font-family: 'Gowun Batang', serif;
    font-size:0.85rem;
  }
  .cardT01{
    left:10%;
  }
  .cardT02{
    left:57%;
  }
  .cardT03{
    top:35rem;
    left:10%;
  }